<template lang="">
  <v-hover v-slot="{ hover }" open-delay="50">  
    <v-card class="card" 
        shaped
        :elevation="hover ? 16 : 2"
        :class="{ 'on-hover': hover }"
    >   
        <v-container >
            <v-row class="title secondary white--text lighten-2">
                     <CustomIcon class="toolicon" v-bind:icon="skill.icon" v-bind:width="67" v-bind:height="67"/>
                 <v-card-title class="text-h6">{{ skill.translatedDescription }} </v-card-title>
            </v-row>  
            <v-row>
                <v-col  cols="12">
                    <SkillSlider v-bind:level="skill.level"  v-bind:like="skill.like"/>
                </v-col>
            </v-row>
        </v-container>
        <v-card-actions>
            <v-btn
                v-if="skill.usage"
                text
                color="secondary"
                @click="reveal = true"
            >
                {{ $t("more information") }}...
            </v-btn>
        </v-card-actions>
        <v-expand-transition>
            <v-card
                v-if="reveal"
                class="transition-fast-in-fast-out v-card--reveal"
                style="height: 100%;"
            >
                <v-card-text class="pb-0">
                    <DescriptionList v-bind:descriptions="skill.translatedUsage" v-bind:title="$t('description')" />
                </v-card-text>
                <v-card-actions class="pt-0">
                    <v-btn text color="secondary" @click="reveal = false">
                        {{ $t("close") }}
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-expand-transition>
    </v-card>
    </v-hover>
</template>
<script>
import SkillSlider from "./utils/SkillSlider";
import CustomIcon from "../utils/CustomIcon";
import DescriptionList from "../utils/DescriptionList";
export default {
    name: "SkillComponent",

    components: {
        SkillSlider,
        CustomIcon,
        DescriptionList,
    },

    props: {
        skill: Object,
    },

    data: () => ({
        reveal: false,
    }),

};
</script>
<style scoped>
.tooldescription {
    margin-top: 10%;
    margin-bottom: 10%;
    text-align: left;
    font-style: italic;
}

.usagetitle .usagedescription {
    margin-left: 5%;
}

.usagetitle .usagedescription {
    margin-left: 5%;
}

.usagetitle {
    margin-top: 5%;
    text-decoration: underline;
}

.v-card--reveal {
    bottom: 0;
    opacity: 1 !important;
    position: absolute;
    width: 100%;
}
</style>
<style lang="sass" scoped>
.v-card.on-hover.theme--dark
  background-color: rgba(#FFF, 0.8)
  >.v-card__text
    color: #000
</style>