<template lang="">
    <v-hover v-slot="{ hover }" open-delay="50">
        <v-card
            class="mx-auto my-12"
            max-width="374"
            :elevation="hover ? 16 : 2"
            :class="{ 'on-hover': hover }"
        >
            <CustomIcon v-bind:height="480" v-bind:icon="imageName" />
            <v-card-title color="secondary">
                {{ title }}
            </v-card-title>
            <v-row align="center" class="mx-0"> </v-row>
            <div v-for="(chapter, i) in chapters" :key="i">
                <v-card-subtitle>
                    {{ chapter.chaptertitle }}
                </v-card-subtitle>
                <v-row align="center" class="mx-0"> </v-row>
                <v-card-text> {{ chapter.chaptercontent }} </v-card-text>
                <v-btn v-if="chapter.attachement"
                    class="ma-2" 
                    outlined 
                    :href="chapter.attachement"
                    download><v-icon>mdi-file-pdf-outline</v-icon>
                        Download PDF
                </v-btn>
            </div>
            
        </v-card>
    </v-hover>
</template>
<script>
import CustomIcon from "./CustomIcon";

export default {
    name: "HobbyCard",

    components: {
        CustomIcon,
    },

    props: {
        title: String,
        chapters: Array,
        imageName: String,
    },
};
</script>
<style lang="sass" scoped>
.v-card.on-hover.theme--dark
  background-color: rgba(#FFF, 0.1)
  >.v-card__text
    color: #000
</style>
