<template lang="">
    <div>
        <v-container >
            <v-row  >
                <v-col cols="6"  >
                    <SkillComponent 
                        v-for="(skill, i) in firstrow(skills)"
                        :key="i"
                        :skill="skill"/>
                </v-col>
                <v-col cols="6" >
                    <SkillComponent 
                        v-for="(skill, i) in secondrow(skills)"
                        :key="i"
                        :skill="skill"/>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import SkillComponent from "./SkillComponent";

export default {
    name: "TwoColumnSkillList",

    components: {
        SkillComponent,
    },

    props: {
        skills: Array,
    },

    methods: {
        firstrow(array) {
            return array.slice(0, array.length / 2);
        },
        secondrow(array) {
            return array.slice(array.length / 2, array.length);
        },
    },
};
</script>
<style lang=""></style>
