<template lang="">
    <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
    >
        <template v-slot:activator="{ on, attrs }">
            <v-btn class="text-subtitle-1 text-sm-h5 text-md-h4 text-lg-h3 text-xl-h2 font-italic" 
                text dark
                v-bind="attrs"
                v-on="on">
                {{ $t("values") }}
            </v-btn>
        </template>

      <v-card>
            <DialogToolBar :dialogClose.sync="dialog" v-bind:title="$t('values')"/>
            <ValueCard 
                v-for="(value, i) in getValues" 
                :key="i" 
                :title="value.title"  
                :imageName="value.image"
                :chapters="value.chapters"
                />
        </v-card>

    </v-dialog>
</template>
<script>
import DialogToolBar from "@/components/utils/DialogToolBar";
import ValueCard from "@/components/utils/CustomCard"
export default {
    name: "DialogValues",

    components: {
        DialogToolBar,
        ValueCard
    },

    props: {
        btncolor: String,
    },

    data() {
        return {
            dialog: false,
            notifications: false,
            sound: true,
            widgets: false,
        };
    },
    computed: {
        getValues() {
            return this.$store.getters.getValues;
        },
    },
};
</script>
