<template lang="">
    <div>
        <div class="text-center d-flex pb-4">
            <v-btn text large @click="all">
                <v-icon large left>mdi-expand-all</v-icon>
                <h3>{{ $t("btn.expand_all") }}</h3>
            </v-btn>

            <v-btn text large @click="none">
                <v-icon large left>mdi-collapse-all</v-icon>
                <h3>{{ $t("btn.collapse_all") }}</h3>
            </v-btn>
        </div>

        <v-expansion-panels v-model="panel" multiple focusable>
            <v-expansion-panel v-for="(skill, i) in getSkills" :key="i">
                <v-expansion-panel-header>
                    <h2 class="skillcategories">{{ skill.translatedCategory }}</h2>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <div class="d-md-none">
                        <SingleColumnSkillList v-bind:skills="skill.content"/>
              
                    </div>
                    <div class="d-none d-md-block">
                        <TwoColumnSkillList v-bind:skills="skill.content"/>
                    </div>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
    </div>
</template>
<script>

import TwoColumnSkillList from "./skillsheet/TwoColumnSkillList"
import SingleColumnSkillList from "./skillsheet/SingleColumnSkillList"
export default {
    name: "SkillSheet",

    components: {
        TwoColumnSkillList,
        SingleColumnSkillList
    },

    data() {
        return {
            panel: [],
            items: 0,
        };
    },

    computed: {
        getSkills() {
            return this.$store.getters.getSkills;
        },
    },

    mounted() {
        this.items = this.$store.getters.getSkills.length;
    },

    methods: {
        all() {
            this.panel = [...Array(this.items).keys()].map((k, i) => i);
        },
        none() {
            this.panel = [];
        },
    },

};
</script>
<style socped>
.skillcategories {
    font-style: italic;
}
</style>
