<template lang="">
    <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
    >
        <template v-slot:activator="{ on, attrs }">            
            <v-btn class="text-subtitle-1 text-sm-h5 text-md-h4 text-lg-h3 text-xl-h font-italic"  text dark v-bind="attrs" v-on="on">
            {{ $t("hobbies") }}            
            </v-btn>
        </template>
  
        <v-card>
            <DialogToolBar :dialogClose.sync="dialog" v-bind:title="$t('hobbies')"/>
            <HobbyCard 
                v-for="(hobby, i) in getHobbies" 
                :key="i" 
                :title="hobby.title"  
                :imageName="hobby.image"
                :chapters="hobby.chapters"
                />
        </v-card>
    </v-dialog>
</template>
<script>
import DialogToolBar from "@/components/utils/DialogToolBar";
import HobbyCard from "@/components/utils/CustomCard"

export default {
    name: "Cooking",

    components: {
        DialogToolBar,
        HobbyCard
    },

    props: {
        btncolor: String,
    }, 

    data() {
        return {
            dialog: false,
            notifications: false,
            sound: true,
            widgets: false,
        };
    },

    computed: {
        getHobbies() {
            return this.$store.getters.getHobbies;
        },
    },
};
</script>
