<template lang="">
<div>
    <v-subheader class="pl-0">

             <div class="maximalLevel">{{ $t("Min like") }}</div>
            <v-spacer></v-spacer>
             <div class="maximalLevel">{{ $t("Max like") }}</div>
    </v-subheader>
    <v-slider
        v-model="level"
        :thumb-size="24"
        thumb-label="always"
        ticks="always"
        :color="color"
        append-icon="mdi-emoticon-cool-outline"
        prepend-icon="mdi-emoticon-cry-outline"
    >
        <template v-slot:thumb-label="{ value }">
            {{
                satisfactionEmojis[Math.min(Math.floor((value * 0.8) / 10), 9)]
            }}
        </template>
    </v-slider>
</div>
</template>
<script>
export default {
    name: "LikeSlider",

    props: {
        level: Number,
        color: String,
    },

    data() {
        return {
            satisfactionEmojis: [
                "☹️",
                "🙁",
                "😐",
                "🙂",
                "😊",
                "😁",
                "😄",
                "😍",
            ],
        };
    },
};
</script>
<style lang=""></style>
